.logo {
  width: 300px;
}

@media only screen and (max-width: 767px) {
  .logo {
    width: 200px !important;
  }
}

.photo {
  border-radius: 0;
}

.card-img {
  border-radius: 0;
}

.card-hr {
  margin: 0;
}

@media (min-width: 576px) {
    .card-columns {
        column-count: 2;
    }
}

@media (min-width: 768px) {
    .card-columns {
        column-count: 3;
    }
}

@media (min-width: 992px) {
    .card-columns {
        column-count: 4;
    }
}

@media (min-width: 1200px) {
    .card-columns {
        column-count: 5;
    }
}
